import React, { Component } from 'react'
import { Table, Spin, Card, notification, Input,Button } from 'antd';
import { getAdminAllLogs, getAdminLogs } from "../../api/request"
import { UserOutlined, SearchOutlined } from '@ant-design/icons';
const columns = [
    {
        title: '',
        dataIndex: 'admin',
    },
    {
        title: '',
        dataIndex: 'opa',
    },
    {
        title: '',
        dataIndex: 'cTime',
    },
];
export default class OpaAllLogs extends Component {
    constructor() {
        super()
        this.state = {
            pagetotal: 10,
            data: [],
            sping: false
        }
    }
    componentDidMount() {
        //
        getAdminAllLogs().then(res => {
            if (res.data.state) {
                //
                this.setState({
                    pagetotal: res.data.data.length
                })
            } else {
                //,
                this.openNotificationWithIcon('error', ",")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        })
        //(,,)
        this.getDatePage(1)
    }
    //
    openNotificationWithIcon = (type, msg) => {
        notification[type]({
            message: msg
        });
    };
    //()
    getDatePage(page) {
        //
        this.setState({
            sping: true
        })
        let data={page}
        //input,,name  
        if (this.tt.state.value){
            data.name = this.tt.state.value
        }
        // console.log(data,"-------");
        getAdminLogs(data).then(res => {
            if (res.data.state) {
                //
                this.setState({
                    data: res.data.data.map(item => {
                        return {
                            key: item._id,
                            admin: item.admin,
                            opa: item.opa,
                            cTime: item.cTime
                        }
                    })
                })
            } else {
                //,!
                this.openNotificationWithIcon('error', ",!")
            }
        }).catch(err => {
            //
            this.openNotificationWithIcon('error', "")
        }).finally(() => {
            //
            this.setState({
                sping: false
            })
        })
    }
    //
    changePage = (page) => {
        this.getDatePage(page)
    }
    //
    searchAdminLogs(value){
        //
        getAdminAllLogs({ name: this.tt.state.value}).then(res => {
            if (res.data.state) {
                //
                this.setState({
                    pagetotal: res.data.data.length
                })
            } else {
                //,
                this.openNotificationWithIcon('error', ",")
            }
        })
        //(,,)
        this.getDatePage(1)
    }
    //
    searchAdmin=(e)=>{
        this.searchAdminLogs(e.target.value)
    }
    //
    buttonSearch=()=>{
        this.searchAdminLogs(this.tt.state.value)
    }
    render() {
        let { data, pagetotal, sping } = this.state
        return (
            <div>
                <Spin spinning={sping}>
                    <Card>
                        <Input placeholder="" style={{ width: "250px" }} prefix={<UserOutlined />} onPressEnter={this.searchAdmin} ref={node=>this.tt=node} />
                        <Button type="primary" onClick={this.buttonSearch} icon={<SearchOutlined />}></Button>
                        <Card hoverable={true} title="" >
                            <Table columns={columns} bordered dataSource={data} size="small" pagination={{ total: pagetotal, onChange: this.changePage }} />
                        </Card></Card>
                </Spin>
            </div>
        )
    }
}
